input[type="checkbox"] {
    //vertical-align: top;
    //margin: 0 0 0 0;
    //width: 0;
    //height: 0;
    + label {
        cursor: pointer;
        margin: 0;
    }
    &:not(checked) {
        position: absolute;
        opacity: 0;
        + label {
            position: relative;
            padding: em(0 0 0 24px, 14px);
            &:before {
                content: '';
                position: absolute;
                top: 0.3em;
                left: 0;
                width: rem(16px);
                height: rem(16px);
                border: 1px solid var(--color-border);
                box-sizing: border-box;
                border-radius: rem(2px);
            }
        }
    }
    &:checked {
        + label {
            &:after {
                @include iconmoon("\e902", rem(12px), 100);
                color: #3171d1;
                position: absolute;
                top: em(6px, 14px);
                left: em(3px, 14px);
            }
        }
    }
}