.table-wrapper {
    overflow: auto;
}
a.lightgallery {
    border-bottom: none;
}
ul.slick-dots li button,
.slick-slide, .slick-arrow {
    outline: none;
}
main {
    display: grid;
    grid-template-columns: 1fr repeat(var(--grid-column), var(--grid-width)) 1fr;
    gap: var(--block-spacing) var(--grid-gap);
    min-height: 100vh;
    align-content: flex-start;
    margin-top: rem(32px);
    @include max-screen(1023px) {
        grid-template-columns: auto repeat(var(--grid-column),var(--grid-width)) auto;
    }
    section {
        grid-column: 2/-2;
    }
    .block {
        grid-column: 2/-2;
    }
}