:root {
    --font-family-main: arial, sans-serif;
    --font-size: 14px;
    --font-size-lg: 16px;
    --font-size-sm: 13px;
    --line-height: 1.6;
    --color-text: #3c4658;
    --color-link: #3c4658;
    --color-border: #dfe1e3;
    --color-primary: #5dc560;
    --color-secondary: #3c4658;
    --color-primary-hover: #00C658;
    --color-block-section: #f4f7fa;

    --grid-column: 12;
    --grid-width: 73px;
    --grid-gap: 24px;
    --block-spacing: 60px;
    @include max-screen(1280px) {
        --grid-width: 59px;
    }

    //@include min-screen(1024px) {
    //    --grid-column: 12;
    //    --grid-width: 60px;
    //    --grid-gap: 24px;
    //    --block-spacing: 60px;
    //}
    //@include min-screen(1280px) {
    //    --grid-width: 73px;
    //}
    //@include max-screen(1023px) {
    //    --grid-column: 4;
    //    --grid-gap: 8px;
    //    --grid-width: 1fr;
    //    --block-spacing: 0px;
    //}
}
*, *:before, *:after {
    box-sizing: inherit;
}
html {
    box-sizing: border-box;
}
body {
    font-family: var(--font-family-main);
    font-size: var(--font-size);
    font-weight: 400;
    color: var(--color-text);
    line-height: var(--line-height); // line-height 24px / var(--font-size)
    min-width: 360px;
    margin: 0;
}
p, ul, ol, table, .ck-tpl, h4, h5, h6, hr {
    margin: rem(24px 0);
}
h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
    color: #000;
}
h1 {
    font-size: map-get($fontSize, h1);
    margin: map-get($margin, h1);
    line-height: map-get($lineHeight, h1);
}
h1 {
    font-size: rem(36px);
    line-height: 1.4;
    margin: rem(60px 0 30px);
    text-transform: uppercase;
    @include max-screen(1023px) {
        font-size: rem(24px);
        line-height: rem(40px);
        margin: rem(32px 0);
    }
}
h2 {
    font-size: rem(32px);
    line-height: rem(48px);
    margin: rem(48px 0 24px);
    color: #23475f;
    @include max-screen(1024px) {
        font-size: rem(24px);
        line-height: rem(40px);
    }
}
h3 {
    font-size: rem(20px);
    line-height: rem(32px);
    margin: rem(48px 0 24px);
}
h4 {
    font-size: var(--font-size);
    line-height: var(--line-height);
}
h5 {
    font-size: var(--font-size);
    line-height: var(--line-height);
}
h6 {
    font-size: var(--font-size);
    line-height: var(--line-height);
}
a {
    color: var(--color-link);
    text-decoration: none;
    border-bottom: 1px solid var(--color-link);
    outline: none;
    &:hover {
        color: var(--color-primary-hover);
        text-decoration: none;
        border-color: transparent;
    }
    &:focus {
        text-decoration: none;
        outline: none;
        border-color: transparent;
    }
    &:active {
        outline: none;
    }
}
b, strong {
    font-weight: 700;
}
p {}
ol,
ul {
    li {
        margin-bottom: rem(16px);
        @include last(1) {
            margin-bottom: 0;
        }
    }
}
ul {
    padding-left: rem(16px);
    li {
        list-style: none;
        position: relative;
        &::before {
            content: "\2022";
            position: absolute;
            top: 0;
            left: rem(-16px);
        }
    }
}
ol {
    padding-left: rem(24px);
}
img {
    max-width: 100%;
    height: auto;
}
hr {
    width: 100%;
    height: 1px;
    border: none;
    background-color: var(--color-border);
}
table {
    width: 100%;
    border-collapse: collapse;
    border-style: solid;
    th,
    td {
        vertical-align: top;
        padding: rem(8px) 1rem;
        border-color: var(--color-1-norma-minus-minus);
        color: var(--color-text);
    }
}
@include max-screen(991px) {
    .table-mobile {
        display: block;
        border: none;
        thead {
            display: none;
        }
        tbody {
            display: block;
        }
        tr {
            display: block;
            border: 1px solid var(--color-border);
            padding: 10px;
            margin-bottom: -1px;
        }
        td, th {
            display: block;
            width: 100% !important;
            padding: 0;
            border: none;
            background-color: transparent !important;
        }
    }
}
