button {
    outline: 0;
    &:focus {
        outline: none;
    }
}
.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-family: var(--font-family-main);
    font-size: rem(14px);
    font-weight: 700;
    line-height: 1.4;
    text-align: center;
    padding: rem(16px 20px);
    border-radius: rem(0);
    text-decoration: none;
    text-transform: uppercase;
    position: relative;
    outline: none;
    user-select: none;
    transition: all 0.2s ease-in-out;
    &:hover {
        text-decoration: none;
    }
    &:focus {
        text-decoration: none;
    }
}
.btn-primary {
    color: #fff;
    background-color: var(--color-primary);
    border: 1px solid var(--color-primary);
    &:hover {
        color: #fff;
        background-color: var(--color-primary-hover);
        border-color: var(--color-primary-hover);
    }
    &:focus,
    &.focus {
        color: #fff;
        background-color: var(--color-primary);
        border-color: var(--color-primary);
    }
}
.btn-secondary {
    color: #fff;
    background-color: var(--color-secondary);
    border: 1px solid var(--color-secondary);
    &:hover {
        color: #fff;
        background-color: var(--color-primary-hover);
        border-color: var(--color-primary-hover);
    }
    &:focus,
    &.focus {
        color: #494949;
        background-color: #ffffff;
        border-color: #494949;
    }
}
.btn-link {
    border: none;
    padding: 0;
    &:hover {
        border: none;
    }
    i {
        transition: all .2s ease-in-out;
    }
}
// ДЛЯ ПОЛУЧЕНИЯ ДОПОЛНИТЕЛЬНЫХ РАЗМЕРОВ
.btn-lg {
    padding: rem(16px 62px);
}
.btn-sm {
    font-size: rem(12px);
    padding: rem(14px 24px);
}
// КНОПКИ БЛОЧНОГО УРОВНЯ – КОТОРЫЕ ЗАНИМАЮТ ПОЛНУЮ ШИРИНУ РОДИТЕЛЬСКОГО ЭЛЕМЕНТА.
.btn-block {
    display: flex;
    width: 100%;
}
// КНОПКА В ВЕРХНЕМ РЕГИСТРЕ
.btn-upper {
    text-transform: uppercase;
}
// ЗАКРУГЛЕННЫЕ КРАЯ
.btn-radius {
    border-radius: rem(4px);
}
// РАСПОЛОЖЕНИЕ ИКОНКИ В КНОПКЕ (СЛЕВА ИЛИ СПРАВА)
.btn-icon-left {
    i {
        margin-right: rem(6px);
    }
    &.btn-link {
        &:hover {
            i {
                transform: translateX(-5px);
            }
        }
    }
}
.btn-icon-right {
    i {
        margin-left: rem(6px);
    }
    &.btn-link {
        &:hover {
            i {
                transform: translateX(5px);
            }
        }
    }
}

